.images::before {
  display: block;
  content: "";
  width: 100%;
  height: 57px;
  background: url("../img/vectors/left.svg") no-repeat;
  position: absolute;
  bottom: -1px;
  transform: translateX(-160px);
  background-position-x: right;
}
@media (min-width: 768px) {
  .images::before {
