    transform: translateX(-600px);
  }
}
.images::after {
  display: block;
  content: "";
  width: 100%;
  height: 57px;
  background: url("../img/vectors/right.svg") no-repeat;
  position: absolute;
  transform: translateX(150px);
  bottom: -1px;
}
